/* 封装axios用于发送请求 */
// 模板代码是从axios官网得到的！！！！！！！！！！
import axios from 'axios'
import { Toast } from 'vant'
import store from '@/store'

// 1.创建一个新的axios实例，以下代码框架（实例创建、请求/相应拦截器等）都可以在axios文档中找到
// 此处是单独创建了一个axios实例，以防污染本身的axios，同时方便未来有多个业务需求时，封装不同的接口文档的信息
const request = axios.create({
  baseURL: 'http://smart-shop.itheima.net/index.php?s=/api',
  timeout: 5000
})

// 2.自定义配置，添加请求、响应拦截器
// 添加请求拦截器
request.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  // 在发送请求后，打开loading
  // 自定义加载图标
  Toast.loading({
    message: '加载中...',
    forbidClick: true,
    loadingType: 'spinner',
    duration: 0 // 用于表示响应未传回来时，系统的提示一直存在
  })
  const token = store.getters.token
  if (token) {
    config.headers['Access-Token'] = token
    config.headers.platform = 'H5'
  }
  return config
}, function (error) {
  // 对请求错误做些什么
  return Promise.reject(error)
})

// 添加响应拦截器
// 对原本的response进行修改，将原生返回的response改成response.data
request.interceptors.response.use(function (response) {
  const result = response.data
  // 相当于扒掉一层，否则后面相应回来的数据处理层级太深
  // 对响应数据做点什么
  console.log(result)
  // 利用拦截器统一处理错误！！！！！！！！！！！！！！！
  if (result.status !== 200) {
    Toast(result.message)
    return Promise.reject(result.message)
  } else {
    Toast.clear() // 登陆页面正常进行时，此时将提示关闭
  }
  return result // 响应拦截器处理一下，减去一层data的包装
}, function (error) {
  // 对响应错误做点什么
  return Promise.reject(error)
})

// 3.将自定义实例导出
export default request // 将request导出
