<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />
    <!-- 购物车开头 -->
     <!-- 当用户登陆并且购物车里有物品时，才显示购物车内容 -->
     <div v-if="this.$store.getters.token&&cartList.length>0">
       <div class="cart-title">
        <span class="all">共<i>{{ cartTotal || 0 }}</i>件商品</span>
        <span class="edit">
          <van-icon name="edit" @click="isEdit=!isEdit"/>
          编辑
        </span>
       </div>

      <!-- 购物车列表 -->
      <div class="cart-list">
        <div class="cart-item" v-for="item in cartList" :key="item.goods_id">
          <van-checkbox :value="item.isChecked" @click="toggleCheck(item.goods_id)"></van-checkbox>
          <div class="show">
            <img :src="item.goods.goods_image" alt="">
          </div>
          <div class="info">
            <span class="tit text-ellipsis-2">{{ item.goods.goods_name }}</span>
            <span class="bottom">
              <div class="price">¥ <span>{{ item.goods.goods_price_min }}</span></div>
              <CountBox :value="item.goods_num" @input="value=>changeCount(value,item.goods_id,item.goods_sku_id)"></CountBox>
              <!-- 此处将实时变化的value值，以及每一项中的id/goods_sku_id等3个实参需要全部获取到，运用箭头函数可以实现 -->
            </span>
          </div>
        </div>
      </div>

      <div class="footer-fixed" @click="toggleAllCheck">
        <div  class="all-check">
          <!-- 小选控制全选按钮 -->
          <van-checkbox  icon-size="18" :value="isAllChecked"></van-checkbox>
          全选
        </div>

        <div class="all-total">
          <div class="price">
            <span>合计：</span>
            <span>¥ <i class="totalPrice">{{ selPrice }}</i></span>
          </div>
          <div v-if="!isEdit" class="goPay" @click="goPay" :class="{disabled:selCount===0}">结算({{ selCount }})</div>
          <div v-else class="delete" :class="{disabled:selCount===0}" @click="handleDel">删除</div>
        </div>
      </div>
     </div>

     <div class="empty-cart" v-else>
        <img src="@/assets/empty.png" alt="">
        <div c lass="tips">
          您的购物车是空的, 快去逛逛吧
        </div>
        <div class="btn" @click="$router.push('/')">去逛逛</div>
     </div>
  </div>
</template>

<script>
import CountBox from '@/components/CountBox.vue'
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'CartPage',
  components: {
    CountBox
  },
  created () {
    if (this.$store.getters.token) {
      this.$store.dispatch('cart/getCartAction')
    }
  },
  data () {
    return {
      isEdit: false
    }
  },
  computed: {
    // 映射[vuex中文件名，state数据名]
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', ['isAllChecked', 'cartTotal', 'selCount', 'selPrice', 'selCartList'])
  },
  methods: {
    toggleCheck (goodsId) {
      this.$store.commit('cart/toggleCheck', goodsId)
    },
    toggleAllCheck () {
      this.$store.commit('cart/toggleAllCheck', !this.isAllChecked)
    },
    changeCount (value, goodsId, skuId) { // 3个形参
      this.$store.dispatch('cart/changeCountAction', {
        value,
        goodsId,
        skuId
      })
    },
    async handleDel () {
      if (this.selCount === 0) return // selCount为mapgetters映射回来的选中商品总数
      await this.$store.dispatch('cart/delSelect')
      this.isEdit = false // 重置一下编辑/删除的状态
    },
    goPay () {
      if (this.selCount > 0) {
        // 跳转时，同时获取并在URL中添加购物车页面被选中的商品id和购物车页面提交模式mode！！！！！！！！！！！！！！！
        this.$router.push({
          path: '/pay',
          query: {
            mode: 'cart',
            cartIds: this.selCartList.map(item => item.id).join(',') // 将获取到的包含多个id值的数组转成字符串储存
          }
        })
      }
    }
  },
  watch: {
    // isEdit 是一个观察函数，它会监视某个数据属性（通常是 data 或 computed 中定义的）的变化。value 是这个数据属性新状态的值
    isEdit (value) { // 此处监听的是data中isEdit的变化情况，value代表isEdit的变化状态
      if (value) {
        this.$store.commit('cart/toggleAllCheck', false) // value为true，则代表编辑状态，进行删除操作，小选框不被选中
      } else {
        this.$store.commit('cart/toggleAllCheck', true) // value为false，则代表非编辑状态，进行结算操作，小选框全部选中
      }
    }

  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }

}

.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
