import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/views/layout'

import Home from '@/views/layout/home'
import Cart from '@/views/layout/cart'
import Category from '@/views/layout/category'
import User from '@/views/layout/user'
import store from '@/store'

// 以下页面使用路由懒加载
const Login = () => import('@/views/login')
const Myorder = () => import('@/views/myorder')
const Pay = () => import('@/views/pay')
const Prodetail = () => import('@/views/prodetail')
const Search = () => import('@/views/search')
const SearchList = () => import('@/views/search/list')
Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      component: Layout,
      redirect: '/home', // 重定向到home页面，意思是点击到layout主页时，自动显示二级路由home页的内容
      children: [
        { path: '/home', component: Home },
        { path: '/category', component: Category },
        { path: '/cart', component: Cart },
        { path: '/user', component: User }
      ]
    }, // 首页，注意：二级路由在一级路由首页下面的children处配置！！！！！！！
    { path: '/login', component: Login },
    { path: '/myorder', component: Myorder },
    { path: '/pay', component: Pay },
    { path: '/prodetail/:id', component: Prodetail },
    { path: '/search', component: Search },
    { path: '/searchlist', component: SearchList }
  ]

})

// 全局前置导航守卫
// 选择放行的页面，有的页面可以直接访问，有的页面带有个人信息，需要登陆之后才能访问
// 3. next() 是否放行
// 如果next()调用，就是放行
// next(路径) 拦截到某个路径页面
const authUrl = ['/pay', '/myorder'] // 定义的为需要权限访问的页面
router.beforeEach((to, from, next) => {
  // 判断需要访问的路径path是否包含在authUri中，如果不在则可以直接访问
  if (!authUrl.includes(to.path)) {
    next()
    return
  }
  const token = store.getters.token
  // 访问到需要权限的页面，获取到token时，直接访问
  // 访问不到时，拦截到login页面
  if (token) { next() } else {
    next('/login')
  }
})

export default router
