import { changeCount, getCartList } from '@/api/cart'
import { Toast } from 'vant'
export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    setCartList (state, newlist) {
      state.cartList = newlist
    },
    toggleCheck (state, goodsId) {
      // 需要先从cartList中通过传过来的id值，找到对应的小选框
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
      // 点击小选框将其取反
    },
    toggleAllCheck (state, flag) {
      // 点击全选按钮，利用总选控制小选，相当于小选全部同步全选框的状态
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    changeCount (state, { goodsId, value }) {
      // 通过id选中修改的商品，再将其数量进行修改
      const obj = state.cartList.find(item => item.goods_id === goodsId)
      obj.goods_num = value
    }
  },
  actions: {
    async getCartActions (context) {
      const { data } = await getCartList()
      data.list.forEach(item => {
        item.isChecked = true
      })
      context.commit('setCartList', data.list)
    },
    async changeCountAction (context, obj) {
      const { goodsId, value, skuId } = obj // 将传过来的实参解构

      // 此处是将实时的参数先更新到页面中 将state通过mutation中定义changeCount进行更新修改
      context.commit('changeCount', {
        goodsId,
        value
      })

      await changeCount(goodsId, value, skuId) // 将传到仓库内的实参传递到服务器里，changeCount是定义在API的接口函数
    },
    async delSelect (context) {
      // 拿到选中的要删除商品列表
      const selCartList = context.getters.selCartList
      // 再拿到要删除商品的列表id
      const cartIds = selCartList.map(item => item.id)
      // 将获取到的要删除的商品id上传服务器接口，此处调用的是服务器接口delSelect
      await this.delSelect(cartIds)
      Toast('删除成功')

      // 重新拉取最新的购物车列表
      context.dispatch('getCartActions')
    }
  },
  getters: {
    isAllchecked (state) {
    // 返回的是一个布尔值，用于判断每个小选是否都被选中，控制全选
      return state.cartList.every(item => item.isChecked)
    },
    // 商品总数，选中商品列表，选中商品总数，选中商品总价
    cartTotal (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 过滤出选中的商品
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 当需要运用别的getters时，只需要直接引用
    selCount (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    selPrice (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num * item.goods.goods_price_min, 0).toFixed(2)
    }
  }
}
