import { setInfo, getInfo } from '@/utiles/storage'
export default {
  namespaced: true,
  state () {
    return {
      userInfo: getInfo() // 页面刷新时，仍能从本地获取到数据
    }
  },
  mutations: {
    setUserInfo (state, obj) {
      state.userInfo = obj
      setInfo(obj) // 将系统返回的数据obj对象存入本地
    }
  },
  actions: {
    logout (context) {
      // 个人信息重置，直接可以在本模块中操作调用setUserInfo
      context.commit('setUserInfo', {}) // 此处传入{}，因为obj为一个对象形式
      // 购物车信息清空，需要跨模块，购物车信息在cart中的setCartList
      context.commit('cart/setCartList', [], { root: true }) // 此处传入[]，因为newList为一个数组形式
    }
  },
  getters: {
  }
}
