// 此文件专门用于配置vant组件库的按需导入，需要将所用到的vant不同种的插件导入，之后将此文件按路径导入main.js文件中
import Vue from 'vue'
import { Checkbox, Icon, Button, Tabbar, TabbarItem, NavBar, Toast, Search, Swipe, SwipeItem, Grid, GridItem, Lazyload, ActionSheet, Dialog, Tab, Tabs } from 'vant'
// 以下所有指令是多种组件的初始化
Vue.use(Tab)
Vue.use(Tabs)
Vue.use(Checkbox)
Vue.use(Dialog)
Vue.use(ActionSheet)
Vue.use(Lazyload)
Vue.use(Toast)
Vue.use(NavBar)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(Button)
Vue.use(GridItem)
Vue.use(Search)
Vue.use(Swipe)
Vue.use(SwipeItem)
Vue.use(Grid)
Vue.use(Icon)
