<template>
  <div class="count-box">
    <button class="minus" @click="handleSub">-</button>
    <input :value="value" class="inp" @change="handleChange" type="text">
     <!-- change事件是在回车和鼠标失去焦点时触发的！！！！！！！！！！！！ -->
    <button class="add" @click="handleAdd">+</button>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    handleSub () {
      if (this.value <= 1) {
        return
      }
      this.$emit('input', this.value - 1)
    },
    handleAdd () {
      this.$emit('input', this.value + 1)
    },
    handleChange (e) {
      const num = +e.target.value // 此处将输入的值转换成数字类型，如果输入的不是数字类型，则返回的为NAN
      // 输入了不合法的文本 或 输入了负值，回退成原来的 value 值
      if (isNaN(num) || num < 1) {
        e.target.value = this.value
        return
      }

      this.$emit('input', num)
    }
  }
}
</script>

<style lang="less" scoped>
.count-box{
  width:110px;
  display: flex;
  .add,.minus{
    width: 30px;
    height: 40px;
    outline: none;
    border: none;
    background-color: #efefef;
  }
  .inp{
    width: 40px;
    height: 40px;
    outline: none;
    border: none;
    background-color: #efefef;
    margin: 0 5px;
    text-align: center;
  }
}

</style>
